import React, {useGlobal} from 'reactn';
import Avatar from './components/Avatar/Avatar';

export default () => {
  const [people] = useGlobal('people');
  return (
    <div id="side-menu">
      {Object.keys(people).map((name, i) => {
        const {avatar, col1: preset} = people[name];
        
        return (
          <Avatar key={i} {...{name, avatar, preset}}/>
        );
      })}
    </div>
  );
};