import React from 'react';

export default ({preset, imgs}) => (
  <div id="share-container">
    <div className="shares">
      <h3 className={"preset" + preset}>Shared images</h3>
      {imgs.map((img, i) => (
        <div key={i} className="fig-container">
          <figure
            style={{
              background: `url(${img})`,
              backgroundSize: 'contain',
              backgroundRepeat: 'no-repeat',
              backgroundPosition: '50%'
            }}></figure>
        </div>
      ))}
    </div>
    <div className="shares">
      <h3 className={"preset" + preset}>Shared files</h3>
      
    </div>
  </div>
)