import React from 'react';

export default ({text, from, place, gap, preset}) => (
  <div
    className="bubble-container"
    style={{
      ...gap ? {marginTop: '32px'} : {}
    }}>
    <div
      className={"bubble preset" + preset}
      style={{
        borderTopLeftRadius: !from || place < 2 ? '32px' : '8px',
        borderTopRightRadius: from || place < 2 ? '32px' : '8px',
        borderBottomLeftRadius: !from || !place || place === 3 ? '32px' : '8px',
        borderBottomRightRadius: from || !place || place === 3 ? '32px' : '8px',
        alignSelf: from ? 'flex-start' : 'flex-end',
        textAlign: from ? 'left' : 'right'
      }}>
      {text}
    </div>
  </div>
);