import React from 'react';

export default ({name, avatar, preset}) => {
  return (
    <div
      className={"avatar preset" + preset}
      style={{
        ...(avatar ? {
          background: `url(${avatar})`,
          backgroundSize: 'cover'
        } : {})
      }}>
      <div className="avatar-text">
        {avatar ? '' : name[0].toUpperCase()}
      </div>
    </div>
  );
}