import React, {useGlobal} from 'reactn';
import Avatar from './components/Avatar/Avatar';
import Name from './components/Name/Name';
import Shares from './components/Shares/Shares';

export default props => {
  const [name] = useGlobal('visiting');
  const [people] = useGlobal('people');
  const {nick, avatar, col1: preset, imgs } = people[name];
  
  return (
    <div id="visiting" {...props}>
      <Avatar {...{name, avatar, preset}}/>
      <Name text={nick || name}/>
      <Shares {...{preset, imgs}}/>
    </div>
  );
};