import React, {useGlobal} from 'reactn';
import {FiSend} from 'react-icons/fi';
import Bubble from './components/Bubble/Bubble';

export default props => {
  const [visiting] = useGlobal('visiting');
  const [people] = useGlobal('people');
  const {col0, col1, msg} = people[visiting];
  
  return (
    <div id="messaging" {...props}>
      <div id="messages">
        {msg.map(([text, from, time], i) => {
          const isFirst = !i || msg[i - 1][1] !== from;
          const isLast = i === msg.length - 1 || msg[i + 1][1] !== from;

          return (
            <Bubble 
              key={i}
              place={isFirst && isLast ? 0 : isFirst ? 1 : isLast ? 3 : 2}
              gap={i && time - msg[i - 1][2] > 2}
              preset={from ? col1 : col0}
              {...{text, from}}/>
          );
        })}
      </div>
      <div id="msg-input">
        <input value="Hey yo wassup meh"/>
        <h1><FiSend/></h1>
      </div>
    </div>
  );
};